import React from 'react'
import Item from './item.js'

const Skill = () => {
    const skillArray = [
        { class: 'iconfont icon-html', title: 'HTML', desc: 'HTMLHTML5(canvasstoragemedia)' },
        { class: 'iconfont icon-css', title: 'CSS', desc: 'CSSCSS3()' },
        { class: 'iconfont icon-javascript', title: 'JS', desc: 'JavascriptJSES6ES7()' },
        { class: 'iconfont icon-react', title: 'React', desc: 'React.jsReact RouterReduxreact' },
        { class: 'iconfont icon-jquery', title: 'jQuery', desc: 'jQueryjQuery UIjQuery Mobile' },
        { class: 'iconfont icon-sass', title: 'Sass', desc: 'cssSass' },
        { class: 'iconfont icon-xiaochengxu', title: '', desc: '' },
        { class: 'iconfont icon-frame', title: '', desc: 'BootstrapMUIWeUIAnt DesignUIangular.jsVue.js' },
        { class: 'iconfont icon-tools', title: '', desc: 'vscodesublimephotoshopnpmwebpackgit' },
        { class: 'iconfont icon-Programming', title: '', desc: 'FortranMatlabC++node.jsphppython' }
    ]
    return (
        <div className="skill-container">
            <h1 className="skill-title"></h1>
            <div className="skill-content">
                {skillArray.map((message) => <Item key={message.class} message={message} />)}
            </div>
        </div>
    )
}

export default Skill


